export function TransformDistance(distance:string){
    if(distance[distance.length-1]==''){
        return distance.replace('','m')
    }
    if(distance[distance.length-1]==''){
        return distance.replace('','h')
    }
    if(distance==''){
        return '1d'
    }
    if(distance==''){
        return '1w'
    }
    if(distance=='M'){
        return '1M'
    }
}